const {createMessageF} = require("../../ui/i18n/message");

const getMembershipProducts = () => {
    const items = (() => {
        const generate = (type, price) => ({
            id: type,
            type,
            name: `Unlimited ${type} Treatment`,
            getPrice: () => ({value: price}),
            getDescription: (locale) => {
                const m = createMessageF(locale)(require("./membership-locales"));
                return m("Monthly");
            },
        });

        return [
            generate("Essential", 200),
            generate("Instant", 200),
            generate("Luxury", 500),
        ];
    })();
    return [
        {
            id: "membership",
            getName: (locale) => {
                const m = createMessageF(locale)(require("./membership-locales"));
                return m("membership");
            },
            directBuy: true,
            onlineProduct: true,
            items,
            checkoutSummary: (purchasing, locale) => {
                const m = createMessageF(locale)(require("./package-locales"));
                const {id} = purchasing.items[0];
                // const itemConfig = items.find((i) => i.id === id);
                // const {type} = itemConfig;
                return [
                    {
                        label: m("Term:"),
                        text: m("Monthly"),
                    },
                    {
                        label: m("Start Date:"),
                        text: "",
                    },
                    {
                        label: m("End Date:"),
                        text: "1/25/2019",
                    },
                ];
            },
        }
    ];
};

exports.getMembershipProducts = getMembershipProducts;


