const shadeColor2 = require("./colors").shadeColor2;
const theming = (company) => ({
    button: {
        normal: {
            backgroundColor: company.booking_widget.color,
            color: company.booking_widget.text_color,
        },
        hover: {
            backgroundColor: shadeColor2(company.booking_widget.color, 0.5),
            color: company.booking_widget.text_color,
        },

    },
    link: {
        normal: {
            color: company.booking_widget.color,
        },
        hover: {
            color: shadeColor2(company.booking_widget.color, 0.5),
        },

    },
});

exports.theming = theming;


