import {formatMsgTemplate} from "./format-msg-template";
import {getPath} from "../../../common/arr-path";
import {createResolveVar} from "./create-resolve-var";
import {createLocaleFunctionLocator} from "./locale-functions";

export const createMessageF = (locale) => {
    const locateLocaleFunction = createLocaleFunctionLocator(locale);

    return (localeMessages) => (fallbackMsg, ...params) => {
        const msg = getPath(localeMessages, [locale, fallbackMsg]);
        if (msg) {
            return formatMsgTemplate(msg, createResolveVar(params, locateLocaleFunction));
        }

        return formatMsgTemplate(fallbackMsg, createResolveVar(params, locateLocaleFunction));
    };
};



