import cln from "classnames";
import React from "react";
import {FComponent} from "../../../../../common/f-component";

import "./date-picker.scss";
import {getMonthWeeks, getWeekDays, sameDate} from "../../../../../../common/date-object";
import {Div} from "../../../../../common/div";
import {createMessageF} from "../../../../../ui/i18n/message";

export class DatePicker extends FComponent {
    state = {
        mode: "week",
    };

    render() {
        const {date, onChange, disabled, ui} = this.props;
        const {mode} = this.state;

        const m = createMessageF(ui.locale)(require("./locales"));

        const renderWeek = (weekDays) => (
            <div className="line" key={`${weekDays[0].month}-${weekDays[0].day}`}>
                {weekDays.map((wd) => (
                    <div
                        key={wd.day}
                        className={cln("day", {
                            disabled: disabled(wd),
                            selected: sameDate(wd, date),
                        })}
                        onClick={() => onChange(wd)}
                    >
                        <Div ui={sameDate(wd, date) && ui.button} className="circle">
                            <label>{wd.day}</label>
                        </Div>
                    </div>
                ))}
            </div>
        );

        return (
            <div className="date-picker">
                <div className="header">
                    <div className="label">
                        {m(monthLabels[date.month])} {date.year}
                    </div>
                    
                    <div className="mode">
                        {["week", "month"].map((item) => (
                            <div
                                className={cln("choice", item, {selected: mode === item})}
                                key={item}
                                onClick={() => this.setState({mode: item})}
                            >
                                {item}
                            </div>
                        ))}
                    </div>
                </div>
                <div className="main">
                    <div className="header">
                        <div className="nav-left">
                            <i className="fas fa-chevron-left"/>
                        </div>
                        <div className="dow-labels">
                            {["S", "M", "T", "W", "T", "F", "S"].map((label, i) => (
                                <label key={i}>{label}</label>
                            ))}
                        </div>
                        <div className="nav-right">
                            <i className="fas fa-chevron-right"/>
                        </div>
                    </div>

                    {mode === "week" ? (
                        renderWeek(getWeekDays(date))
                    ) : (
                        getMonthWeeks(date).map((weekDays) => (
                            renderWeek(weekDays)
                        ))
                    )}
                </div>
            </div>
        );
    }
}

const monthLabels = [
    "January", "February", "March", "April", "May", "June", "July",
    "August", "September", "October", "November", "December"
];



