const globalKeys = {
    onKeyDown: (check, invoke) => {
        const listener = (e) => {
            // console.log(e.keyCode);
            if (check(e)) {
                invoke();
                e.preventDefault();
                e.stopPropagation();
            }
        };
        window.addEventListener("keydown", listener);

        return () => window.removeEventListener("keydown", listener);
    },
};

exports.globalKeys = globalKeys;


