const mapKeys = (obj, fn) => {
    if (obj == null) {
        return null;
    }

    const ret = {};
    for (const k in obj) {
        ret[fn(k, obj[k])] = obj[k];
    }
    return ret;
};

exports.mapKeys = mapKeys;

const mapValues = (obj, fn) => {
    if (obj == null) {
        return null;
    }

    const ret = {};
    for (const k in obj) {
        ret[k] = fn(obj[k], k);
    }
    return ret;
};

exports.mapValues = mapValues;

const omit = (obj, ...keys) => {
    let ret = {};
    for (const k in obj) {
        if (keys.indexOf(k) === -1) {
            ret[k] = obj[k];
        }
    }
    return ret;
};

exports.omit = omit;



