import cln from "classnames";
import React from "react";

import "./layout.scss";
import {LayoutLoading} from "./layout-loading";

export class Layout extends React.Component {

    render() {
        const {children, className, onClose, loading,
            extraHeaderRight, extraHeaderLeft,
            ui,
        } = this.props;

        return (
            <div className="layout">
                <div className="header">
                    <div className={cln("content-left", {"has-extra": extraHeaderLeft})}>
                        {ui.companyLogoBig ? (
                            <img className="icon" src={ui.companyLogoBig}/>
                        ) : (
                            <div className="company-name">
                                {ui.companyName}
                            </div>
                        )}
                        {extraHeaderLeft && (
                            <div className="extra">
                                {extraHeaderLeft}
                            </div>
                        )}
                    </div>

                    <div className="content-right">
                        {extraHeaderRight && (
                            <div className="extra">
                                {extraHeaderRight}
                            </div>
                        )}

                        <button className="close-button" onClick={onClose}/>
                    </div>

                </div>

                <div className={cln("content", className)}>
                    {children}
                </div>

                {loading && (
                    <LayoutLoading message={loading}/>
                )}
            </div>
        );
    }
}



