const createTimeObject = () => {
    return parseDate(new Date());
};

exports.createTimeObject = createTimeObject;

const getWeekDays = (date) => {
    const start = toDate(getWeekStart(date)).getTime();

    return new Array(7).fill(0).map((_, i) => {
        const wd = new Date(start);
        wd.setDate(wd.getDate() + i);
        return parseDate(wd);
    });
};
exports.getWeekDays = getWeekDays;

const getWeekStart = (date) => {
    const d = toDate(date);

    const start = new Date(d.getTime());
    start.setDate(start.getDate() - d.getDay());
    return parseDate(start);
};

const getMonthWeeks = (date) => {
    const d = toDate(date);

    const start = new Date(d.getTime());
    start.setDate(start.getDate() - d.getDate());

    const end = new Date(d.getTime());
    end.setMonth(end.getMonth() + 1);
    end.setDate(-1);

    let weeks = [];
    let index = toDate(getWeekStart(parseDate(start))).getTime();
    for (;index < end.getTime();) {
        weeks.push(getWeekDays(parseDate(index)));
        index += 7 * 24 * 60 * 60 * 1000;
    }
    return weeks;
};
exports.getMonthWeeks = getMonthWeeks;

const toDate = (date) => new Date(date.year, date.month, date.day);

exports.toDate = toDate;

const parseDate = (date) => {
    date = typeof date === "number" ? new Date(date) : date;

    return ({
        year: date.getFullYear(),
        month: date.getMonth(),
        day: date.getDate(),
    });
};

// exports.parseDate = parseDate;

const sameDate = (d1, d2) =>
    d1.day === d2.day &&
    d1.month === d2.month &&
    d1.year === d2.year
;

exports.sameDate = sameDate;


