import React from "react";
import {FComponent} from "../../../common/f-component";

import "./checkout.scss";
import {Payment} from "./payment/payment";

export class Checkout extends FComponent {
    constructor(props, context) {
        super(props, context);

        this.state = {
            note: null,
        };
    }

    render() {
        const {
            purchasing,

            onBack, ui,

            render,
        } = this.props;

        const {note} = this.state;

        const total = calculateTotal(purchasing.items);

        return render({
            payment: (
                <Payment
                    onBook={() => {}}
                    total={total}
                    ui={ui}
                />
            ),
            note,
            onChangeNote: (note) => this.setState({note}),
            priceSummary: {total, subTotal: total},
        });
    }
}

const calculateTotal = (items) =>
    items.reduce((t, item) => t + (item.price.value || 0), 0)
;



