
export function getPath(obj, path) {
    if (path == null) {
        return obj;
    }
    for (const v of path) {
        if (obj == null) {
            return obj;
        }
        obj = obj[v];
    }
    return obj;
}

export function setPath(object, path, value) {
    if (path.length === 0) {
        return value;
    }

    let [attr, ...lastAttrs] = path;

    if (Array.isArray(object)) {
        let clone = object ? object.slice(0) : [];
        clone[attr] = setPath(object && object[attr], lastAttrs, value);
        return clone;
    } else {
        return Object.assign({}, object, {[attr]: setPath(object && object[attr], lastAttrs, value)});
    }

}

export function changePath(object, path, fn) {
    let oldValue = getPath(object, path);
    let updatedValue = fn(oldValue);
    return updatedValue !== oldValue ? setPath(object, path, updatedValue) : object;
}


export const locate = (obj, path) => {
    if (obj == null) {
        return null;
    }
    const [first, ...last] = path;

    const ret = ((cond) => {
        if (typeof cond === "object") {
            return obj.find((item) => !Object.keys(cond).find((k) => cond[k] !== item[k]));
        } else {
            return obj[cond];
        }
    })(first);

    if (last.length) {
        return locate(ret, last);
    } else {
        return ret;
    }
};



