import React, {Fragment} from "react";

import {Layout} from "../../../layout/layout";
import {clientHeader} from "../../../common/client-header/client-header";

import "./checkout-layout.scss";
import {A} from "../../../common/a";
import {renderDetails} from "../../steps/common/details/render-details";
import {AccountSections} from "./account-sections/account-sections";
import {formatMoney, formatPrice} from "../../steps/common/formats";
import {createMessageF} from "../../../ui/i18n/message";

export const checkoutLayout =
    ({child, onChangeChild, onBack, location, childService, clientView, employee, ui,
         purchaseGroup, purchasing,
    }) =>
    ({payment, note, onChangeNote, priceSummary}) => {

    const m = createMessageF(ui.locale)(require("./locales"));

    const getItemName = (item) => purchaseGroup.itemBlueprint == null ? item.name : purchaseGroup.itemBlueprint.getName(item, ui.locale);

    return (
        <Layout
            extraHeaderRight={
                clientHeader(clientView, ui)
            }
            ui={ui}
        >
            <div className="checkout-layout-d923g92f3">
                <div className="main">
                    <h1>{m("Checkout")}</h1>

                    {!clientView.client ? (
                        <AccountSections
                            onSelected={(client) => clientView.onChange(client)}
                            ui={ui}
                        />
                    ) : (
                        payment
                    )}

                    <div className="back">
                        <A ui={ui.link} onClick={onBack}>{m("Back")}</A>
                    </div>
                </div>
                <div className="side-bar">
                    <section className="summary">
                        <h4 className="section-title">
                            {purchaseGroup.appointmentType ?
                                m("Appointment Details") :
                                m("Purchase Summary")
                            }
                        </h4>

                        {renderItemsSection({items: purchasing.items, getItemName})}

                        {renderDetails({
                            lines: [
                                ... purchaseGroup.checkoutSummary ? purchaseGroup.checkoutSummary(purchasing, ui.locale) : [],
                                child && {
                                    label: m("Pet breed:"),
                                    text: child.breed.option_value,
                                },
                                employee && {
                                    label: m("Booking with:"),
                                    text: employee.name,
                                },
                                purchaseGroup.concernClient && {
                                    label: m("Customer type:"),
                                    text: clientView.client == null || !clientView.client.is_existing_customer ? (
                                        m("I'm new to this business")
                                    ) : (
                                        m("I've been here before")
                                    ),
                                },
                                // time && {
                                //     label: m("Time:"),
                                //     text: m("$time($0)", time.begin_at),
                                // },
                                // duration != null && {
                                //     label: m("Duration:"),
                                //     text: m("$mins($0)", duration),
                                // },

                                ...(purchaseGroup.onlineProduct ? [] : [
                                    location && {
                                        label: m("Your location:"),
                                        text: `${location.street_address}, ${location.city}, ${location.state} ${location.zip_code}`,
                                    },
                                    // duration: items.reduce((t, i) => t + i.duration, 0),
                                ]),
                            ],
                            className: "details",
                        })}

                        {!purchaseGroup.onlineProduct && (
                            <textarea
                                className="note"
                                value={note || ""}
                                onChange={(e) => onChangeNote(e.target.value)}
                                placeholder={m("Add a note about this appointment")}
                            />
                        )}

                        {renderPriceSummary(priceSummary, ui)}
                    </section>
                </div>
            </div>
        </Layout>
    );
};


const renderItemsSection = ({items, getItemName}) => (
    <div className="services-f42j93">
        {items.map((item) => (
            <div className="item" key={item.id}>
                <div className="name">
                    {getItemName(item)}
                </div>
                <div className="price">
                    {formatPrice(item.price)}
                </div>
            </div>
        ))}
    </div>
);

const renderPriceSummary = (priceSummary, ui) => {
    const m = createMessageF(ui.locale)(require("./locales"));
    return (
        <div className="price-summary">
            <div className="line">
                <div className="label">{m("Subtotal:")}</div>
                <div className="value">
                    {formatMoney(priceSummary.subTotal)}
                </div>
            </div>
            <div className="line">
                <div className="label">{m("Tax:")}</div>
                <div className="value">
                    {formatMoney(priceSummary.total - priceSummary.subTotal)}
                </div>
            </div>
            <div className="line">
                <div className="label total">{m("Total:")}</div>
                <div className="value total">
                    {formatMoney(priceSummary.total)}
                </div>
            </div>
        </div>
    );
};


