const {omit} = require("../../../common/objects");

const applyPriceClient = (pricing, client, varId) => {
    const maxPrice = {
        value: pricing.new_list_price,
        min: pricing.new_list_price != pricing.new_min_price ? pricing.new_min_price : null,
    };

    if (!client) {
        return maxPrice;
    }

    if (client.package_purchase_variation_ids.indexOf(varId) > -1) {
        return {in_package: true};
    }

    if (client.membership_package_purchase_variation_ids.indexOf(varId) > -1 ) {
        return {in_membership: true};
    }

    if (client.is_existing_customer) {
        return {
            value: pricing.existing_list_price,
            min: pricing.existing_list_price != pricing.existing_min_price ? pricing.existing_min_price : null,
        };
    } else {
        return maxPrice;
    }
};
exports.applyPriceClient = applyPriceClient;

const applyPriceTime = (price, time) => {
    return omit(price, "min");
};
exports.applyPriceTime = applyPriceTime;


