import React from "react";
import {formatMoney, formatNumber} from "../../location-purchase-form/steps/common/formats";

function alphabet(locale) {
    return locale === "en" || locale === "fr";
}

export const createLocaleFunctionLocator = (locale) => {
    const plural = (noun) => {
        if (alphabet(locale)) {
            return `${noun}s`;
        } else {
            return noun;
        }
    };
    const functionMap = {
        "count": (count, noun) => {
            if (alphabet(locale)) {
                return `${count} ${count === 1 ? noun : plural(noun)}`;
            } else {
                return `${count}${count === 1 ? noun : plural(noun)}`;
            }
        },
        "plural": plural,
        "cap": (str) => {
            if (alphabet(locale)) {
                return str.split(" ").map((s) => s[0].toUpperCase() + s.substring(1)).join(" ")
            } else {
                return str;
            }
        },
        "mins": mins(locale),
        "time": (time) => {
            return !alphabet(locale) ? "星期一7/30/2018晚上9点" : `Mon 7/30/2018 at 9:00 am`;
        },
        "money": (value) => {
            return alphabet(locale) ? formatMoney(value) : formatNumber(value) + "美元";
        },
        "i": (str) => React.createElement("i", {}, str),
        "b": (str) => React.createElement("b", {}, str),
    };
    return (fnName) => functionMap[fnName];
};


const mins = (locale) => {
    const hoursLabels = {
        en: " Hour",
        fr: " Heur",
        "zh": "小时",
    };
    const minsLabels = {
        en: " Min",
        fr: " Min",
        "zh": "分",
    };

    const minLabel = minsLabels[locale];
    const hourLabel = hoursLabels[locale];

    return (duration) => {

        const {mins, hours} = toMins(duration);

        if (hours) {
            return `${hours}${hourLabel} ${mins}${minLabel}`;
        } else {
            return `${mins}${minLabel}`;
        }
    };
};

function toMins(duration) {
    const hours = Math.floor(duration / 60);
    if (hours === 0) {
        return {mins: Math.floor((duration))};
    } else {
        const mod = duration - hours * 60;
        return {
            hours,
            mins: Math.floor(mod),
        };
    }
}


