import React, {Fragment} from "react";

import {Layout} from "../../../layout/layout";
import {clientHeader} from "../../../common/client-header/client-header";
import {renderLocationSection} from "./location-section/render-location-section";
import cln from "classnames";
import {renderChildSection} from "./child-section/render-child-section";

import "./index-layout.scss";

export const indexLayout = ({child, onChangeChild, onCancel, location, childService, clientView, ui}) => ({services, showErrors, employeeSection, cart, sidebar, footer}) => (
    <Layout
        extraHeaderRight={
            <Fragment>
                {cart}

                {clientHeader(clientView, ui)}
            </Fragment>
        }
        ui={ui}
    >
        <div className="index-layout-rfj2392g">
            <div className="side-bar">
                {renderLocationSection({location, onChangeLocation: onCancel, ui,})}
                {sidebar}
            </div>

            <div className="main">
                <div className="content">

                    <div className={cln("emp-n-child", {"has-child": childService})}>
                        <div className="col">
                            {employeeSection}
                        </div>

                        {childService && (
                            <div className="col">
                                {renderChildSection({
                                    child,
                                    onChange: onChangeChild,
                                    childService,
                                    ui,
                                    showErrors,
                                })}
                            </div>
                        )}
                    </div>

                    {services}
                </div>
            </div>

            {footer}
        </div>
    </Layout>
);



