import React from "react";
import cln from "classnames";
import {A} from "../../../../../common/a";
import {createMessageF} from "../../../../../ui/i18n/message";

export const renderSubGroupFilter = ({purchaseGroup: {id, subGroups}, active, onSelect, avaiItem, ui}) => {

    const m = createMessageF(ui.locale)(require("./locales"));

    const renderSubGroup = (subGroup) => {
        const count = avaiItem == null ? subGroup.items.length :
            subGroup.items.reduce((t, v) => t + (avaiItem(v) ? 1 : 0), 0)
        ;
        return (
            <div
                className={cln("filter-line", {
                    active: active != null && active(subGroup),
                    disabled: !count,
                })}
                key={subGroup.id}
                onClick={() => count && onSelect(subGroup)}
            >
                <A ui={ui.link} className="name">
                    {subGroup.getName(ui.locale)}
                </A>

                {!!count && (
                    <div className="summary">
                        {m("$count($0, 'Service')", count)}
                    </div>
                )}
            </div>
        );
    };

    return (
        <section className="filter filter-by-service" key={id}>
            <h4 className="section-title">{m("Filter By Service")}</h4>

            <div className="content">
                <div
                    className={cln("filter-line", {active: active == null})}
                    onClick={() => onSelect(null)}
                >
                    <A ui={ui.link} className="name">
                        {m("All Services")}
                    </A>
                    <div className="summary">
                        {m("$count($0, 'Service')", subGroups.reduce((t, {items}) => t+items.length, 0))}
                    </div>
                </div>

                {subGroups.map(renderSubGroup)}
            </div>
        </section>
    );
};



