import React from "react";
import {FComponent} from "../common/f-component";
import {ComposePurchase} from "./steps/compose-purchase/compose-purchase";
import {indexLayout} from "./layouts/index/index-layout";
import {timeLayout} from "./layouts/time/time-layout";
import {Checkout} from "./steps/checkout/checkout";
import {checkoutLayout} from "./layouts/checkout/checkout-layout";
import {blueprintLayout} from "./layouts/blueprint/blueprint-layout";
import {devMode} from "../common/dev-mode";

export class LocationPurchaseForm extends FComponent {
    constructor(props, context) {
        super(props, context);

        this.state = {
            child: null,
            purchasing: null,
        };

        if (DEV_MODE) {
            devMode.alterLocationPurchaseForm(this);
        }

        // if (DEV_QUAN || DEV_LUAN) {
        //     setTimeout(() => {
        //         const {deals, employees} = this.props.locationPackage;
        //         this.setState({
        //             data: {
        //                 // variations: [deals[0].variations[0]],
        //                 employee: employees[0],
        //                 time: openTimesMock[0],
        //                 child: {breed: companyMock["Petco Grooming"].custom_fields[3].custom_field_options[0]},
        //             }
        //         });
        //     }, 10);
        // }
        //
        // if (DEV_MODE) {
        //     this.onUnmount(globalKeys.onKeyDown(
        //         keyCombo("meta+0"),
        //         () => this.setState({data: null}))
        //     );
        // }
    }

    render() {

        const {
            locationPackage,
            onCancel,
            clientView,
            ui,
        } = this.props;
        const {child, purchasing,} = this.state;

        const {location, employees, childService, getPurchaseGroups} = locationPackage;

        const layoutParams = {
            child, onChangeChild: (child) => this.setState({child}),
            location, childService, clientView, ui, onCancel,
        };

        const purchaseGroups = getPurchaseGroups(clientView.client, child);
        if (!purchasing) {
            return (
                <ComposePurchase
                    ref={(ref) => this.composePurchaseForm = ref}
                    purchaseGroups={purchaseGroups}
                    employees={employees}

                    onDone={(purchasing) => this.setState({purchasing})} // Show checkout form
                    ui={ui}

                    render={({layout, ...others}) =>
                        composePurchaseLayouts[layout](layoutParams)(others)
                    }
                />
            );
        } else {
            const pg = purchaseGroups.find((pg) => pg.id === purchasing.groupId);
            return (
                <Checkout
                    {...{
                        purchasing,
                        ui,
                    }}
                    onChangeChild={(child) => this.setState({child})}

                    render={(checkoutContent) =>
                        checkoutLayout({
                            ...layoutParams,
                            purchasing,
                            purchaseGroup: pg,
                            employee: employees.find((e) => e.id === purchasing.employee_id),
                            onBack: () => this.setState({purchasing: null}, () =>
                                pg.directBuy == false &&
                                this.composePurchaseForm.setPurchasing(purchasing)
                            ),
                        })(checkoutContent)
                    }
                />
            );
        }
    }
}

const composePurchaseLayouts = {
    "index": indexLayout,
    "time": timeLayout,
    "blueprint": blueprintLayout,
};



