const {createMessageF} = require("../../ui/i18n/message");

const getGiftCardProducts = () => {
    return [
        {
            id: "gift-card",
            onlineProduct: true,
            getName: (locale) => {
                const m = createMessageF(locale)(require("./gift-card-locales"));
                return m("gift card");
            },
            itemBlueprint: {
                getName: (item, locale) => {
                    const m = createMessageF(locale)(require("./gift-card-locales"));
                    return m("$cap($0)", m("gift card"));
                },
            },
            checkoutSummary: (purchasing, locale) => {

                const m = createMessageF(locale)(require("./gift-card-locales"));
                const {to, from, recipient_email, message} = purchasing.items[0].data;
                return [
                    {
                        label: m("Recipient:"),
                        text: to,
                    },
                    {
                        label: m("From:"),
                        text: from,
                    },
                    {
                        label: m("Sent to:"),
                        text: recipient_email,
                    },
                    message && {
                        label: m("Message:"),
                        text: message,
                    },
                ];
            },
        }
    ];
};

exports.getGiftCardProducts = getGiftCardProducts;


