import React from "react";
import cln from "classnames";
import {A} from "../../../../../common/a";
import {createMessageF} from "../../../../../ui/i18n/message";

export const renderEmpFilter = ({employees, filterEmp, onSelect, avaiEmp, ui}) => {

    const m = createMessageF(ui.locale)(require("./locales"));

    const renderEmp = (emp) => (
        <div
            className={cln("filter-line", {
                active: filterEmp === emp,
                disabled: !avaiEmp(emp),
            })}
            key={emp.id}
            onClick={() => avaiEmp(emp) && onSelect(emp)}
        >
            <A ui={ui.link} className="name">
                {emp.name}
            </A>
        </div>
    );

    return (
        <section className="filter filter-by-service">
            <h4 className="section-title">{m("Filter By Staff Members")}</h4>

            <div className="content">
                <div
                    className={cln("filter-line", {active: filterEmp == null})}
                    onClick={() => onSelect(null)}
                >
                    <A ui={ui.link} className="name">
                        {m("All Staff Members")}
                    </A>
                </div>

                {employees.map(renderEmp)}
            </div>
        </section>
    );
};



