const switchAmong = (arr, fn) => {
    const getArr = typeof arr === "function" ? arr : () => arr;

    let index = 0;

    return () => {
        index++;
        const a = getArr();
        if (index >= a.length) {
            index = 0;
        }
        return fn(a[index]);
    };
};

exports.switchAmong = switchAmong;


