import React from "react";
import {FComponent} from "../../../../../common/f-component";

import "./time-picker.scss";
import {paddingLeft} from "../../../../../../common/strings";
import {formatPrice} from "../../../common/formats";
import {Button} from "../../../../../common/button";
import {A} from "../../../../../common/a";
import {createMessageF} from "../../../../../ui/i18n/message";

export class TimePicker extends FComponent {

    render() {
        const {loading, times, onSelect, ui} = this.props;

        const m = createMessageF(ui.locale)(require("./locales"));

        return (
            <div className="time-picker">
                {loading ? (
                    <div className="loading">
                        <i className="fas fa-circle-notch fa-spin"/>
                    </div>
                ) : times == null ? (
                    <div className="error">
                        {m("Sorry, there are no appointments for this date.")}

                        <div className="next-link">
                            <A ui={ui.link} >{m("Go to the next available date.")}</A>
                        </div>
                    </div>
                ) : (
                    times.map((time) => (
                        <div
                            className="item"
                            key={time.begin_at}
                            onClick={() => onSelect(time)}
                        >
                            <div className="time">
                                {formatTime(new Date(time.begin_at))}
                            </div>
                            <div className="price">
                                {formatPrice({
                                    value: time.list_price,
                                    min: time.list_price == time.min_price ? null : time.list_price,
                                })}
                            </div>

                            <Button ui={ui.button}>{m("Select")}</Button>
                        </div>
                    ))
                )}

            </div>
        );
    }
}

function formatTime(time) {
    return `${time.getHours()}:${paddingLeft(time.getMinutes())}`;
}



