import cln from "classnames";
import React from "react";
import {FComponent} from "../../../common/f-component";

//import "./";

export class AsyncAlias extends FComponent {

    constructor(props, context) {
        super(props, context);

        this.state = {
            loading: true,
            value: null,
        };

        this.load(props.src);
    }

    load(src) {
        this.setState({
            loading: true,
        });

        this.props.translate(this.state.currentSrc).then((value) => {
            if (!this.props.equals(src, this.props.src)) {
                return; // Reject result as we are not looking for this any more
            }

            this.setState({
                loading: false,
                value,
            })
        });
    }

    componentDidUpdate(prevProps) {
        if (!this.props.equals(prevProps.src, this.props.src)) {
            this.load(this.props.src);
        }
    }

    render() {
        const {render} = this.props;
        const {loading, value} = this.state;

        return render(value, loading);
    }
}



