
const {getServiceProducts} = require("./products/service-products");
const {getPackageProducts} = require("./products/package-products");
const {getMembershipProducts} = require("./products/membership-products");
const {getGiftCardProducts} = require("./products/gift-card");

const {createIndex} = require("../../common/indexing");
const {listPricingsApi} = require("../api/pricing-api");
const {listDealsApi} = require("../api/deal-api");
const {listEmployeesApi} = require("../api/employee-api");

export const createLocationPackage = async (location, company) => {

    const [
        employees,
        deals,
        pricings,
    ] = await Promise.all([
        listEmployeesApi(location.id),
        listDealsApi(location.id),
        listPricingsApi(location.id),
    ]);

    const pricingIndex = createIndex(pricings, "variation_id", "employee_id");

    return {
        location,
        employees,
        childService: getChildService(company),
        getPurchaseGroups: (client, child) => {

            return [
                ... getServiceProducts({deals, client, child, pricingIndex}),
                ... getPackageProducts(),
                ... getMembershipProducts(),
                ... getGiftCardProducts(),
            ];
        },
    };
};


const getChildService = (company) => {
    if (company.child_type == null) {
        return null;
    }
    return {
        breeds: company.custom_fields.find((cf) => cf.uuid === "pet_breed").custom_field_options
    };
};


