import cln from "classnames";
import React, {Fragment} from "react";
import {FComponent} from "../../../../common/f-component";

import "./payment.scss";
import {createForm} from "bee-form-react";
import {required} from "bee-form-validators";

import {renderFGInput, renderFormGroup} from "../../common/form/forms";
import {formatMoney} from "../../common/formats";
import {Button} from "../../../../common/button";
import {PaymentInfo} from "./payment-info";
import {renderPayOnlineToggle} from "../pay-online-toggle/pay-online-toggle";
import {chain} from "../../../../../common/fs";
import {createMessageF} from "../../../../ui/i18n/message";

const formConfig = {
    "pet-info.child_breed": [],
    "pet-info.child_name": [required], // human name

    "client-info.mobile_phone": [required], // phone
    "client-info.dob": [required], // date
};

export class Payment extends FComponent {
    constructor(props, context) {
        super(props, context);

        this.state = {
            payOnline: false,
            showErrors: false,
        };

        this.form = createForm(formConfig);
        this.form.onChange(() => this.forceUpdate());
    }

    book(data) {
        const {onBook} = this.props;
    }

    render() {
        const {ui} = this.props;
        const {payOnline, showErrors} = this.state;

        const fv = this.form.createView();

        const m = createMessageF(ui.locale)(require("./locales"));
        return (
            <div className="payment">
                <div className="method">
                    {renderPayOnlineToggle({
                        value: payOnline,
                        onChange: (payOnline) => this.setState({payOnline}),
                        ui,
                    })}
                </div>

                {renderPetInfo({
                    fv: fv.scope("pet-info"),
                    ui,
                    showErrors,
                })}

                {renderClientInfo({
                    fv: fv.scope("client-info"),
                    ui,
                    showErrors,
                })}

                <PaymentInfo
                    {...({
                        fv: fv.scope("payment-info"),
                        ui,
                    })}
                />

                <footer className="">
                    <div className="text">
                        {m("Total")}: {formatMoney(this.props.total)}
                    </div>
                    <div className="controls">
                        <Button
                            ui={ui.button}
                            onClick={() => fv.hasError() ?
                                (!showErrors && this.setState({showErrors: true})) :
                                this.book(fv.getData())
                            }
                        >{m("Complete Booking")}</Button>
                    </div>
                </footer>
            </div>
        );
    }
}

const renderPetInfo = ({ui, fv, showErrors}) => chain(createMessageF(ui.locale)(require("./locales")), (m) => (
    <section className="pet-info">
        <h4 className="section-title">{m("Pet Information")}</h4>

        {/*{fv.withControl("child_breed", ({getError, getValue, pushValue}) => renderFormGroup("Dog Breed", showErrors && getError(), (*/}
            {/*<Select*/}
                {/*value={getValue()}*/}
                {/*valueToLabel={(v) => ({"US": "United States", "CA": "Canada"}[v])}*/}
                {/*list={["US", "CA"]}*/}
                {/*onChange={(value) => pushValue(value)}*/}
            {/*/>*/}
        {/*)))}*/}

        {fv.withControl("child_name", renderFGInput(m("Dog Name"), null, showErrors))}
    </section>
));

const renderClientInfo = ({ui, fv, showErrors}) => chain(createMessageF(ui.locale)(require("./locales")), (m) => (
    <section className="client-info">
        <h4 className="section-title">{m("Customer Information")}</h4>

        {fv.withControl("mobile_phone", ({bind, getError}) => (
            renderFormGroup(m("Mobile Phone"), showErrors && getError(), (
                <div className="">
                    <input
                        type="tel"
                        {...bind()}
                    />

                    <div className="help">
                        {m("Contact information is $i('only') shared with merchants you book, so they can reach you if something comes up.")}
                    </div>
                </div>
            ))
        ))}
        {fv.withControl("dob", renderFGInput(m("Date Of Birth"), null, showErrors))}
    </section>
));


