import cln from "classnames";
import React from "react";
import {createMessageF} from "../../../../../ui/i18n/message";

export const renderSocialLoginSection = ({onLoggedIn, ui}) => {
    const m = createMessageF(ui.locale)(require("./locales"));
    return (
        <section className="social-login">
            <h4 className="section-title">{m("Continue With")}</h4>

            <div className="content">
                <button
                    className="fb"
                    onClick={() => onLoggedIn({
                        id: 14431, first_name: "Quan", last_name: "FB",
                        package_purchase_variation_ids: [],
                        membership_package_purchase_variation_ids: [],
                    })}
                >Facebook
                </button>

                <div className="desc">
                    {m("Nothing you do will be posted on Facebook without permission.")}
                </div>
            </div>
        </section>
    );
};


