import cln from "classnames";
import React from "react";

export const renderFormGroup = (label, hasError, control) => (
    <div className={cln("form-group", {"has-error": hasError})}>
        <div className="control-label">
            {label}
        </div>

        {control}
    </div>
);
export const renderFGInputBase = (label, placeholder, type, showErrors) => ({bind, getError}) => renderFormGroup(label, showErrors && getError(), (
    <input
        placeholder={placeholder}
        type={type}
        {...bind()}
    />
));
export const renderFGInput = (label, placeholder, showErrors) => renderFGInputBase(label, placeholder, undefined, showErrors);
export const renderFGInputPassword = (label, placeholder, showErrors) => renderFGInputBase(label, placeholder, "password", showErrors);



