import React, {Fragment} from "react";
import {Select} from "../../../steps/common/select/select";
import {A} from "../../../../common/a";

import "./render-child-section.scss";

export const renderChildSection = ({child, onChange, childService, ui, showErrors}) => {

    return (
        <section className="child">
            <h4 className="section-title">Please Select a pet</h4>

            {child == null ? (
                <Select
                    valueToLabel={(breed) => breed.option_value}
                    list={childService.breeds}
                    onChange={(breed) => onChange({breed})}
                    placeholder="Select breed"
                />
            ) : (
                <div className="">
                    <div className="">
                        {child.breed.option_value}
                    </div>
                    <A ui={ui.link} className="">
                        Change breed
                    </A>
                </div>
            )}

            {showErrors && !child && (
                <div className="error">
                    Please select
                </div>
            )}
        </section>
    );
};



