import React, {Fragment} from "react";
import {FComponent} from "../../../../common/f-component";

import {renderSubGroupFilter} from "./filter-sub-group/render-sub-group-filter";
import {renderEmpFilter} from "./filter-employee/render-emp-filter-section";
import {renderPurchaseItemsGroup} from "./sub-groups/render-purchase-items-group";
import {renderEmpSection} from "./employee/render-emp-section";
import {HeaderCart} from "../../../layouts/index/header-cart/header-cart";
import {locate} from "../../../../../common/arr-path";
import {Button} from "../../../../common/button";
import {createMessageF} from "../../../../ui/i18n/message";
import {locatePurchaseItem} from "../locate-purchase-item";
import {renderGroupFilter} from "./filter-group/render-group-filter-section";

export class StepIndex extends FComponent {
    constructor(props, context) {
        super(props, context);

        this.state = {
            filterSubGroup: null,
            filterGroup: null,
            showErrors: false,
        };
    }

    render() {
        const {
            purchasing, onChangePurchasing,

            employee, onChangeEmployee,

            purchaseGroups,

            employees,

            onDone, ui,

            render,
        } = this.props;

        const {filterSubGroup, showErrors, filterGroup} = this.state;

        function locatePI(pi) {
            return locatePurchaseItem(pi, purchaseGroups, purchasing);
        }

        const avaiEmp = (emp) => {


            if (filterSubGroup != null) {
                const subGroupConfig = locate(purchaseGroups, [{id: filterSubGroup.groupId}, "subGroups", {id: filterSubGroup.subGroupId}]);
                if (!subGroupConfig.items.find((item) => item.getPrice(emp))) {
                    return false;
                }
            }

            if (purchasing == null) {
                return true;
            }

            return !purchasing.items.find((pi) => locatePI(pi).getPrice(emp) == null);

        };

        const m = createMessageF(ui.locale)(require("./locales"));

        const getItemPriceP2 = (purchaseGroup) => purchaseGroup.concernEmployee ? (
            (item) => {
                const emp = employee ? employee : employees.find((emp) => avaiEmp(emp) && item.getPrice(emp) !== null);
                return emp == null ? null : item.getPrice(employee);
            }
        ) : (
            (item) => item.getPrice()
        );

        const commonPgRenderProps = (purchaseGroup) => ({
            isSelected: (item) => purchasing && purchasing.items.find((pi) => pi.id === item.id),
            somethingSelected: !!purchasing,
            appointmentType: purchaseGroup.appointmentType,
            directBuy: purchaseGroup.directBuy,
            ui,
        });

        const renderGroupWithSubGroups = (purchaseGroup) => {
            const getPrice = getItemPriceP2(purchaseGroup);

            const show = (subGroup) => {
                return (!filterSubGroup || subGroup.id === filterSubGroup.subGroupId) && subGroup.items.find(getPrice);
            };

            return (
                purchaseGroup.subGroups.map((subGroup) => !show(subGroup) ? null : (
                    renderPurchaseItemsGroup({
                        ... commonPgRenderProps(purchaseGroup),
                        group: subGroup,
                        getPrice,
                        onAdd: (item, subGroup) => (purchaseGroup.directBuy ? onDone : onChangePurchasing)({
                            groupId: purchaseGroup.id,
                            items: [
                                ...(purchasing && purchasing.items || []),
                                {id: item.id, subGroupId: subGroup.id,}
                            ],
                        }),
                    })
                ))
            );
        };

        const renderGroupWithItems = (purchaseGroup) => {
            const getPrice = getItemPriceP2(purchaseGroup);

            return !purchaseGroup.items.find(getPrice) ? null : (
                renderPurchaseItemsGroup({
                    ... commonPgRenderProps(purchaseGroup),
                    group: purchaseGroup,
                    getPrice,
                    onAdd: (item) => (purchaseGroup.directBuy ? onDone : onChangePurchasing)({
                        groupId: purchaseGroup.id,
                        items: [
                            ...(purchasing && purchasing.items || []),
                            {id: item.id,}
                        ],
                    }),
                })
            );
        };

        const filteredPurchaseGroups = purchaseGroups.filter(
            (purchaseGroup) =>
                (!filterSubGroup || filterSubGroup.groupId === purchaseGroup.id) &&
                (!filterGroup || purchaseGroup.id == filterGroup.id) &&
                (!purchasing || purchasing.groupId === purchaseGroup.id) &&
                (!employee || purchaseGroup.concernEmployee)
        );


        return render({
            sidebar: (
                <Fragment>
                    {filteredPurchaseGroups.filter((pg) => pg.subGroups).map((pg) => (
                        renderSubGroupFilter({
                            purchaseGroup: pg,
                            active: filterSubGroup && filterSubGroup.groupId === pg.id &&
                                ((subGroup) => subGroup.id == filterSubGroup.subGroupId),
                            onSelect: (subGroup) => this.setState({
                                filterSubGroup: subGroup && {
                                    groupId: pg.id,
                                    subGroupId: subGroup.id,
                                }
                            }),
                            avaiItem: employee == null ? null : ((item) => item.getPrice(employee)),
                            ui,
                        })
                    ))}

                    {filteredPurchaseGroups.find((pg) => pg.concernEmployee) && (
                        renderEmpFilter({
                            employees,
                            filterEmp: employee,
                            onSelect: onChangeEmployee,
                            avaiEmp,
                            ui,
                        })
                    )}

                    {(filteredPurchaseGroups.length > 1 || filterGroup != null) && (
                        renderGroupFilter({
                            groups: purchaseGroups,
                            isSelected: (pg) => filterGroup && filterGroup.id === pg.id,
                            onSelect: (pg) => {
                                if (!pg.itemBlueprint) {
                                    return this.setState({filterGroup: {id: pg.id}});
                                } else {
                                    onDone({groupId: pg.id, items: [{}]});
                                }
                            },
                            ui,
                        })
                    )}
                </Fragment>
            ),
            employeeSection: filteredPurchaseGroups.find((pg) => pg.concernEmployee) && (
                renderEmpSection({
                    employee,
                    onChange: onChangeEmployee,
                    employees: employees.filter(avaiEmp),
                    ui,
                })
            ),
            showErrors,
            services: (
                <section>
                    {filteredPurchaseGroups.map((purchaseGroup) => (
                        purchaseGroup.subGroups ? (
                            renderGroupWithSubGroups(purchaseGroup)
                        ) : purchaseGroup.items && (
                            renderGroupWithItems(purchaseGroup)
                        )
                    ))}
                </section>
            ),
            footer: (
                <Fragment>
                    {purchasing && (
                        <footer className="">
                            <div className="text">
                                {m("$count($0, 'service') selected", purchasing.items.length)}
                            </div>
                            <div className="controls">
                                <Button
                                    ui={ui.button}
                                    onClick={() => {
                                        onDone();
                                        this.setState({showErrors: true});
                                    }}
                                >
                                    {m("Select Time")}
                                </Button>
                            </div>
                        </footer>
                    )}
                </Fragment>
            ),
            cart: (
                <HeaderCart
                    {...{purchasing, onDone, ui}}
                    getName ={(pi) => locatePI(pi).name}
                    getPrice={(pi) => locatePI(pi).getPrice(employee)}
                    onChangePurchasing={onChangePurchasing}
                />
            )
        });
    }

    static getDerivedStateFromProps(props, state) {
        return null;
    }
}


