import React from "react";

import "./pick-location-page.scss";
import {FComponent} from "../common/f-component";
import {getLocationsApi} from "../api/get-locations-api";
import {Layout} from "../layout/layout";
import {createIndex} from "../../common/indexing";
import {countryStates} from "../../common/country-states";
import {clientHeader} from "../common/client-header/client-header";
import {A} from "../common/a";
import {createMessageF} from "../ui/i18n/message";

export class PickLocationPage extends FComponent {

    constructor(props, context) {
        super(props, context);

        this.state = {
            locations: null,
            selecting: false,
        };

        getLocationsApi(props.companyId).then((locations) => this.setState({locations}));

        this.onChanged("companyId", (companyId) => {
            getLocationsApi(companyId).then((locations) => this.setState({locations}));
        })
    }

    select(location) {
        const {onSelect} = this.props;

        this.setState({selecting: true});
        onSelect(location).then(() => {
            this.setState({selecting: false});
        });
    }

    render() {
        const {ui, clientView} = this.props;
        const {selecting, locations} = this.state;

        const m = createMessageF(ui.locale)(require("./locales"));

        if (locations == null) {
            return (
                <Layout loading={m("Loading locations...")} ui={ui}/>
            );
        }

        let locationsByState = createIndex(locations, (location) => `${location.country}-${location.state}`);

        return (
            <Layout
                className="pick-location-page"
                loading={ selecting ? "Please wait..." : null }
                extraHeaderRight={clientHeader(clientView, ui)}
                ui={ui}
            >
                <div className="title">
                    {m("Pick a Location")}
                </div>

                <div className="locations">
                    {Object.keys(locationsByState).map((key) => {
                        const [countryCode, stateCode] = key.split("-");
                        const stateLocations = locationsByState[key];
                        return (
                            <div className="state-row" key={key}>
                                <div className="state-name">
                                    {countryStates[countryCode][stateCode]}
                                </div>

                                {stateLocations.map((location) => (
                                    <div className="address-line" key={location.id}>
                                        <div className="city">
                                            <A ui={ui.link} onClick={() => this.select(location)}>
                                                {location.city}
                                            </A>
                                        </div>

                                        <div className="full-address">
                                            <a onClick={() => this.select(location)}>
                                                {location.street_address}
                                                {location.street_address_2 && " " + location.street_address_2}
                                                {", "}{location.city}
                                                {", "}{location.state}
                                                {" "}{location.zip_code}
                                            </a>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        );
                    })}
                </div>
            </Layout>
        );
    }
}


