import React from "react";
import {FComponent} from "../../../common/f-component";

import {StepIndex} from "./step-index/step-index";
import {StepTime} from "./step-time/step-time";
import {locate} from "../../../../common/arr-path";
import {locatePurchaseItem} from "./locate-purchase-item";
import {StepBlueprint} from "./step-blueprint/step-blueprint";
import {devMode} from "../../../common/dev-mode";

export class ComposePurchase extends FComponent {
    constructor(props, context) {
        super(props, context);

        this.state = {
            purchasing: null,
            employee: null,
            step: "index",
        };

        if (DEV_MODE) {
            devMode.alterComposePurchase(this);
        }
    }

    render() {
        const {
            onDone,

            purchaseGroups,

            employees,

            ui,

            render,
        } = this.props;

        const { step, purchasing, employee} = this.state;

        if (step === "index") {
            return (
                <StepIndex
                    {...{
                        purchaseGroups,

                        employees,

                        ui,

                        purchasing, employee,
                    }}
                    onDone={(purchasing) => {
                        const newPurchasing = purchasing || this.state.purchasing;
                        const pg = purchaseGroups.find((g) => g.id === newPurchasing.groupId);
                        if (pg.appointmentType) {
                            this.setState({
                                purchasing: newPurchasing,
                                step: "time"
                            });
                        } else if (pg.itemBlueprint) {
                            this.setState({
                                purchasing: newPurchasing,
                                step: "blueprint",
                            });
                        } else {
                            this.props.onDone(freezePurchasing(newPurchasing, purchaseGroups, employee && employee.id));
                        }

                    }}
                    onChangePurchasing={(purchasing) => this.setState({purchasing})}
                    onChangeEmployee={(employee) => this.setState({employee})}
                    render={(content) =>
                        render({
                            layout: "index",
                            ...content,
                        })
                    }
                />
            );
        } else if (step === "time") {
            return (
                <StepTime
                    {...{
                        employee,
                        ui,
                    }}
                    employees={employees}
                    onSelect={(time) => this.selectTime(time)}
                    items={purchasing.items.map((pi) => {
                        const piConfig = this.locatePI(pi);
                        return {
                            id: pi.id,
                            name: piConfig.name,
                            duration: piConfig.duration,
                            getPrice: (employee) => piConfig.getPrice(employee),
                            remove: () => this.setState({
                                step: purchasing.items.length === 1 ? "index" : step,
                                purchasing: {...purchasing, items: purchasing.items.filter((i) => pi !== i)},
                            }),
                        };
                    })}
                    onChangeEmployee={(employee) => this.setState({employee})}

                    render={(content) =>
                        render({
                            layout: "time",
                            onBack: () => this.setState({step: "index"}),
                            ...content,
                        })
                    }
                />
            );
        } else {
            return (
                <StepBlueprint
                    ui={ui}
                    onDone={(data) => {
                        const {amount, ...others} = data;
                        return onDone({...purchasing, items: [{
                            ...purchasing.items[0],
                            id: null,
                            data: others,
                            price: {value: +amount},
                        }]});
                    }}
                    render={(content) =>
                        render({
                            layout: "blueprint",
                            onBack: () => this.setState({purchasing: null, step: "index"}),
                            ...content,
                        })
                    }
                />
            );
        }
    }

    locatePI = (pi) => {
        const {purchaseGroups} = this.props;
        const {purchasing} = this.state;

        return locatePurchaseItem(pi, purchaseGroups, purchasing);
    };


    static getDerivedStateFromProps(props, state) {
        const purchasing = state.purchasing;
        if (purchasing) {
            const purchaseGroups = props.purchaseGroups;
            const pg = purchaseGroups.find((pg) => pg.id === purchasing.groupId);
            if (pg == null ||
                (!pg.itemBlueprint && purchasing.items.find((pi) => locatePurchaseItem(pi, purchaseGroups, purchasing) == null))
            ) {
                return {purchasing: null, employee: null, step: "index"};
            }
        }

        return null;
    }

    setPurchasing(purchasing) {
        const {employees} = this.props;
        this.setState({
            purchasing,
            employee: employees.find((e) => e.id === purchasing.employee_id),
            step: purchasing.time ? "time" : "index",
        });
    }

    selectTime(time) {
        const {onDone, purchaseGroups} = this.props;
        const {purchasing, employee} = this.state;

        onDone({
            ...freezePurchasing(purchasing, purchaseGroups, employee && employee.id),
            time,
        });
    }
}

const freezePurchasing = (purchasing, purchaseGroups, employee_id) => {
    // console.log(JSON.stringify(purchasing));
    return ({
        ...purchasing,
        items: purchasing.items.map((item) => {
            const itemConfig = locatePurchaseItem(item, purchaseGroups, purchasing);
            return ({
                ...item,
                name: itemConfig.name,
                duration: itemConfig.duration,
                price: itemConfig.getPrice(employee_id),
            });
        }),
        employee_id,
    });
};



