const paddingLeft = (num, length = 2) => {
    num = "" + num;

    for (;num.length < length;) {
        num = "0" + num;
    }
    return num;
};

exports.paddingLeft = paddingLeft;

const paddingRight = (num, length = 2) => {
    num = "" + num;

    for (;num.length < length;) {
        num += "0";
    }
    return num;
};

exports.paddingRight = paddingRight;


