const mapValues = require("./objects").mapValues;

const createIndex = (col, getKeyList) => {
    if (getKeyList.length === 0) {
        return col;
    }

    const [getKey, ...last] = getKeyList;

    let ret = {};
    for (const e of col) {
        let key = getKey(e);
        let list = ret[key];
        if (list == null) {
            list = [];
            ret[key] = list;
        }
        list.push(e);
    }

    return mapValues(ret, (list) => createIndex(list, last));
};

exports.createIndex = (col, ...args) =>
    createIndex(col, normalizeArgs(args))
;

const normalizeArgs = (args) => {
    return args.map((a) => typeof a === "function" ? a : ((e) => e[a]));
};


