import React, {Fragment} from "react";
import cln from "classnames";
import {formatPrice} from "../../../common/formats";
import {Button} from "../../../../../common/button";
import {button} from "../../../../common/button";
import {css} from "emotion";
import {createMessageF} from "../../../../../ui/i18n/message";

export const renderPurchaseItemsGroup = ({group, getPrice, onAdd, isSelected, appointmentType, directBuy, somethingSelected, ui}) => {

    const m = createMessageF(ui.locale)(require("./locales"));

    return (
        <Fragment key={group.id}>
            <h4 className="section-title">{m("$plural($0)", group.getName(ui.locale))}</h4>

            <div className={sGroup}>
                {group.items.map((item) => {
                    const price = getPrice(item);

                    return (
                        <div
                            className={cln("item", {selected: isSelected(item), disabled: price == null})}
                            key={item.id}
                        >
                            <div className="name">
                                {item.name}
                            </div>
                            <div className="description">
                                {item.getDescription(ui.locale)}
                                {" | "}
                                {price == null ? "N/A" : formatPrice(price)}
                            </div>

                            <Button
                                onClick={() => !isSelected(item) && price && onAdd(item, group)}
                                ui={ui.button}
                            >
                                {
                                    isSelected(item) ? m("Added") :
                                    somethingSelected ? m("Add") :
                                    appointmentType ? m("Book") :
                                        m("Buy")
                                }
                            </Button>
                        </div>
                    );
                })}
            </div>
        </Fragment>
    );
};

const sGroup = css`
    box-shadow: 0 0 4px 0 #ccc;
    padding: 0 12px;
    background: #fff;
    border-radius: 3px;
    
    .item {
      padding: 12px 0;
      vertical-align: baseline;
      position: relative;
    
      &:not(:last-child) {
        border-bottom: 1px solid #ccc;
      }
    
      .name {
        color: #333;
        text-transform: capitalize;
        font-size: 16px;
        letter-spacing: .24px;
        line-height: 24px;
        max-width: 400px;
      }
    
      .description {
        color: rgba(51,51,51,.5);
        font-size: 14px;
        letter-spacing: .21px;
        line-height: 25px;
      }
    
      button {
        position: absolute;
        top: 12px;
        right: 0;
    
        ${button}
    
    
        &:hover {
          background: darken(rgb(75, 116, 144), 15%);
        }
      }
    
      &.disabled {
        .name, .description, button {
          opacity: 0.3;
        }
        button {
          background-color: rgb(75, 116, 144);
          cursor: not-allowed;
        }
      }
    
      &.selected button {
        opacity: .3;
        cursor: default;
        background-color: rgb(75, 116, 144);
      }
    }
`;


