import cln from "classnames";
import React from "react";
import {FComponent} from "../../../../common/f-component";

import "./account-sections.scss";
import {A} from "../../../../common/a";
import {CreateAnAccountForm} from "./create/create-an-account-form";
import {renderSocialLoginSection} from "./social-login/social-login";
import {createMessageF} from "../../../../ui/i18n/message";

export class AccountSections extends FComponent {

    render() {
        const {ui, onSelected} = this.props;

        return (
            <div className="account-sections">
                {renderSocialLoginSection({
                    onLoggedIn: onSelected,
                    ui,
                })}

                {renderCreateAnAccountSection({
                    onCreated: onSelected,
                    ui,
                })}
            </div>
        );
    }
}

const renderCreateAnAccountSection = ({onCreated, ui}) => {
    const m = createMessageF(ui.locale)(require("./create/locales"));
    return (
        <section className="create-an-account">
            <h4 className="section-title">{m("Create an Account")}</h4>

            <div className="already">
                {m("Already have an account?")}
                {" "}
                <A ui={ui.link}>{m("Sign in")}</A>
            </div>

            <CreateAnAccountForm
                onCreated={onCreated}
                ui={ui}
            />
        </section>
    );
};


