const {createMessageF} = require("../../ui/i18n/message");

const getPackageProducts = () => {
    const items = (() => {
        const generate = (type, price) => ({
            id: type,
            name: `5 ${type} Treatments`,
            count: 5,
            type,
            getPrice: () => ({value: price}),
            getDescription: (locale) => {
                const m = createMessageF(locale)(require("./package-locales"));
                return m("$count($0, $1)", 5, m("Appointment"));
            },
        });

        return [
            generate("Essential", 100),
            generate("Instant", 100),
            generate("Luxury", 200),
        ];
    })();
    return [
        {
            id: "package",
            getName: (locale) => {
                const m = createMessageF(locale)(require("./package-locales"));
                return m("package");
            },
            directBuy: true,
            onlineProduct: true,
            items,
            checkoutSummary: (purchasing, locale) => {
                const m = createMessageF(locale)(require("./package-locales"));
                const {id} = purchasing.items[0];
                const itemConfig = items.find((i) => i.id === id);
                const {count, type} = itemConfig;
                return [
                    {
                        label: m("Appointments:"),
                        text: count,
                    },
                    {
                        label: m("Expires:"),
                        text: m("Valid for appointments before Dec 06, 2018"),
                    },
                    {
                        label: m("Can be used for:"),
                        text: m("$0 Treatments", m(type)),
                    },
                ];
            },
        },
    ];
};

exports.getPackageProducts = getPackageProducts;


