
export function createResolveVar(params, locateLocaleFunction) {
    return (varName) => {
        if (!isNaN(varName)) {
            return params[varName];
        }
        const fn = locateLocaleFunction(varName);

        if (!fn) {
            throw `Can not resolve locale function [${varName}]`;
        }

        return fn;
    };
}


