import React, {Fragment} from "react";
import cln from "classnames";
import {A} from "../../../../common/a";
import "./pay-online-toggle.scss";
import {chain} from "../../../../../common/fs";
import {createMessageF} from "../../../../ui/i18n/message";


export const renderPayOnlineToggle = ({value, onChange, ui}) => {

    const m = createMessageF(ui.locale)(require("./locales"));
    const list = [
        {
            label: m("Pay Online"),
            value: true,
            help: (
                <Fragment>
                    {m("Credit cards are not charged until your appointment takes place. Debit cards are charged at the time of booking ")}
                    {" "}
                    (<A ui={ui.link}>{m("Learn More")}</A>)
                </Fragment>
            )
        },
        {
            label: m("Pay In Person"),
            value: false,
            help: (
                m("You'll pay the business at the time of your appointment. However, we require a credit card to hold your appointment and enforce the cancellation policy below.")
            ),
        },
    ];

    return (
        <div className="pay-online-toggle">
            <div className="toggles">
                {list.map((item) => (
                    <div
                        className={cln("toggle", {selected: value === item.value})}
                        key={item.value}
                        onClick={() => onChange(item.value)}
                    >
                        {item.label}
                    </div>
                ))}
            </div>

            <div className="help">
                {list.find((i) => i.value === value).help}
            </div>
        </div>
    );
};



