import React from "react";
import {FComponent} from "./common/f-component";
import {PickLocationPage} from "./pick-location/pick-location-page";
import {createLocationPackage} from "./location-package/location-package";
import {LocationPurchaseForm} from "./location-purchase-form/location-purchase-form";
import {globalKeys} from "../common/global-keys";
import {keyCombo} from "../common/key-combo";
import {switchAmong} from "../common/switch-among";
import clientMock from "./api/client-mock";
import companyMock from "./api/company-mock";
import locationMock from "./api/location-mock";
import {createUi} from "./ui/ui";

export class MpWidget extends FComponent {
    constructor(props, context) {
        super(props, context);

        this.state = {
            company: null,
            locationPackage: null,
            client: null,
        };
        if (DEV_MODE) {
            const languages = [
                "en",
                "fr",
                "zh",
            ];

            const companies = [
                "Kiosk Demo",
                "Blind Barber",
                "Petco Grooming",
            ];
            this.state.company = companyMock[companies[0]];

            if (DEV_QUAN||DEV_LUAN) {
                (async () => this.setState({locationPackage: await createLocationPackage(locationMock[companies[0]][0], this.state.company)}))();

                this.setState({client: clientMock.normal,});
            }

            this.onUnmount(globalKeys.onKeyDown(
                keyCombo("F9"),
                switchAmong(companies, async (key) => {
                    this.setState({company: companyMock[key]});

                    if (this.state.locationPackage) {
                        this.setState({locationPackage: await createLocationPackage(locationMock[key][0], this.state.company)})
                    }
                })
            ));
            this.onUnmount(globalKeys.onKeyDown(keyCombo("shift+F9"), switchAmong(languages, async (lang) =>
                this.setState({company: {...this.state.company, locale_uuid: lang}})
            )));
            this.onUnmount(globalKeys.onKeyDown(keyCombo("meta+F9"), switchAmong([null, clientMock.with_package, clientMock.normal], (client) =>
                this.setState({client})
            )));
        }
    }
    render() {
        const {company, locationPackage, client} = this.state;

        const clientView = {
            client,
            onChange: (client) => this.setState({client}),
        };

        const ui = createUi(company);

        if (!locationPackage) {
            return (
                <PickLocationPage
                    companyId={company.id}
                    onSelect={(location) => createLocationPackage(location, company)
                        .then((locationPackage) => this.setState({locationPackage}))
                    }
                    clientView={clientView}
                    ui={ui}
                />
            );
        } else {
            return (
                <LocationPurchaseForm
                    key={locationPackage.location.id}
                    locationPackage={locationPackage}
                    onCancel={() => this.setState({locationPackage: null})}
                    clientView={clientView}
                    ui={ui}
                />
            );
        }
    }
}



