import React from "react";
import {FComponent} from "../../../../common/f-component";

import "./header-cart.scss";
import {HeaderCartExpand} from "./expand";
import {TransitionGroup, CSSTransition} from "react-transition-group";

export class HeaderCart extends FComponent {
    constructor(props, context) {
        super(props, context);

        this.state = {
            expand: false,
            // expand: true,
        };
    }

    render() {
        const {purchasing, onChangePurchasing, onSelectTime, getPrice, getName, ui} = this.props;
        const {expand} = this.state;

        return (
            <div
                className="header-cart"
                onMouseOver={() => this.setState({expand: true})}
                onMouseLeave={() => this.setState({expand: false})}
            >
                <div className="toggle">
                    <div className="value">
                        {purchasing == null ? 0 : purchasing.items.length}
                    </div>
                </div>

                <TransitionGroup>
                    {expand && (
                        <CSSTransition
                            classNames={"up"}
                            timeout={300}
                        >
                            <HeaderCartExpand
                                purchasing={purchasing}
                                onChangePurchasing={onChangePurchasing}
                                onSelectTime={onSelectTime}
                                getPrice={getPrice}
                                getName={getName}
                                onClose={() => this.setState({expand: false})}
                                ui={ui}
                            />
                        </CSSTransition>
                    )}
                </TransitionGroup>
            </div>
        );
    }
}



