import cln from "classnames";
import React from "react";
import {FComponent} from "../../../../common/f-component";

import "./select.scss";
import {anyAction} from "./any-action";

export class Select extends FComponent {
    constructor(props, context) {
        super(props, context);

        this.state = {
            expand: false,
        };

        this.onUnmount(
            anyAction(() => this.state.expand && this.setState({expand: false}))
        );
    }

    render() {
        const {value, list, valueToLabel, onChange, placeholder, thin} = this.props;
        const {expand} = this.state;

        return (
            <div className={cln("select", {expand, thin})}>
                <div
                    className={cln("toggle", {placeholder: value == null && placeholder})}
                    onClick={() =>
                        setTimeout(() => this.setState({expand: !expand}), 0)
                    }
                >
                    <div className="text">
                        {value == null && placeholder ? placeholder : valueToLabel(value)}
                    </div>

                    <div className="select-arrow"/>
                </div>

                {expand && (
                    <div className="expand">
                        {list.map((item, i) => (
                            <div
                                className={cln("item", {active: item == value})}
                                key={i}
                                onClick={() => item != value && onChange(item)}
                            >
                                {valueToLabel(item)}
                            </div>
                        ))}
                    </div>
                )}
            </div>
        );
    }
}



