const {locate} = require("../../../../common/arr-path");

const locatePurchaseItem = (pi, purchaseGroups, purchasing) => {
    let o = locate(purchaseGroups, [{id: purchasing.groupId}]);
    if (pi.subGroupId) {
        o = locate(o, ["subGroups", {id: pi.subGroupId}]);
    }
    o = locate(o, ["items", {id: pi.id}]);
    return o;
};
exports.locatePurchaseItem = locatePurchaseItem;



