import React from "react";
import cln from "classnames";
import {A} from "../../../../../common/a";
import {createMessageF} from "../../../../../ui/i18n/message";

export const renderGroupFilter = ({groups, isSelected, onSelect, ui}) => {
    const m = createMessageF(ui.locale)(require("./locales"));

    const renderGroup = (group) => (
        <div
            className={cln("filter-line", {
                active: isSelected(group),
            })}
            key={group.id}
            onClick={() => onSelect(group)}
        >
            <A ui={ui.link} className="name">
                {group.getName(ui.locale)}
            </A>

            {!group.itemBlueprint && (
                <div className="summary">
                    {m(
                        "$count($0, $1)",
                        group.subGroups ?
                            group.subGroups.reduce((t, sg) => t + sg.items.length, 0) :
                            group.items.length,
                        group.getName(ui.locale)
                    )}
                </div>
            )}
        </div>
    );

    return (
        <section className="filter filter-by-service">
            <h4 className="section-title">{m("Filter By Type")}</h4>

            <div className="content">
                {groups.map(renderGroup)}
            </div>
        </section>
    );
};



