const keyCombo = (pattern) => {
    const {masks, keyCode} = parsePattern(pattern);

    const mask = masks.reduce((r, m) => ({...r, [m]: true}), {
        shift: false,
        alt:   false,
        meta:  false,
        ctrl:  false,
    });

    return (e) => {
        if (Object.keys(mask).find((attr) => e[attr+"Key"] != mask[attr])) {
            return false;
        }
        return keyCode === e.keyCode;
    }
};

exports.keyCombo = keyCombo;

const parsePattern = (pattern) => {
    pattern = pattern.toLowerCase();
    const match = /(\^|ctrl\+|shift\+|alt\+|meta\+)(.+)/.exec(pattern);
    if (match) {
        const next = parsePattern(match[2]);
        return {
            masks: [masks[match[1]], ...next.masks],
            keyCode: next.keyCode,
        };
    } else {
        return {masks: [], keyCode: keyCodes[pattern]};
    }
};

const keyCodes = {
    "f9": 120,
    "0": 48,
};

const masks = {
    "^": "ctrl",
    "ctrl+": "ctrl",
    "shift+": "shift",
    "alt+": "alt",
    "meta+": "meta",
};


