const createClient = (client) => {
    return new Promise((resolve, reject) => {
        setTimeout(() => {
            resolve({
                ...client, id: 42523,
                package_purchase_variation_ids: [],
                membership_package_purchase_variation_ids: [],
            });
        }, 1000);
    });

};

exports.createClient = createClient;


