import React, {Fragment} from "react";

import {Layout} from "../../../layout/layout";
import {clientHeader} from "../../../common/client-header/client-header";

import "./blueprint-layout.scss";
import {A} from "../../../common/a";
import {createMessageF} from "../../../ui/i18n/message";

export const blueprintLayout =
    ({child, onChangeChild, location, childService, clientView, ui}) =>
    ({onBack, content}) => {

    const m = createMessageF(ui.locale)(require("./locales"));

    return (
        <Layout
            extraHeaderRight={
                clientHeader(clientView, ui)
            }
            ui={ui}
        >
            <div className="blueprint-layout-dj239g239">
                <div className="main">
                    <div className="content">
                        {content}
                    </div>

                    <div className="back">
                        <A ui={ui.link} onClick={onBack}>{m("Back")}</A>
                    </div>
                </div>
            </div>
        </Layout>
    );
};



