const {createMessageF} = require("../../ui/i18n/message");
const {applyPriceClient} = require("../price/cal-price");

const getServiceProducts = ({deals, pricingIndex, client, child}) => {

    const getPricings = (empId, varId) => {
        const byVar = pricingIndex[varId];
        if (byVar == null) {
            return null; // No employee can do this variation
        }

        if (empId == null) {
            // noinspection LoopStatementThatDoesntLoopJS
            for (const empId in byVar) {
                return byVar[empId];
            }
        } else {
            return byVar[empId];
        }
    };

    const getUserPrice = (empId, varId) => {
        const pricings = getPricings(empId, varId);
        if (pricings == null) {
            return null;
        }
        const pricing = pricings[0];

        return applyPriceClient(pricing, client, varId);
        // const priceClient = applyPriceClient(pricing, client, varId);
        // return data && data.time ? applyPriceTime(priceClient, data.time) : priceClient;
    };
    return [
        {
            id: "service",
            getName: (locale) => {
                const m = createMessageF(locale)(require("../locales"));
                return m("service");
            },
            concernClient: true,
            concernEmployee: true,
            appointmentType: true,
            subGroups: deals.filter((d) => !d.is_addon).map((deal) => ({
                id: deal.id,
                getName: () => deal.service.name,
                items: deal.variations.map((variation) => ({
                    id: variation.id,
                    name: variation.name,
                    getPrice: (employee) => getUserPrice(employee && employee.id, variation.id),
                    duration: variation.duration,
                    getDescription: (locale) => {
                        const m = createMessageF(locale)();
                        return m("$mins($0)", variation.duration);
                    }
                })),
            })),
        }
    ];
};

exports.getServiceProducts = getServiceProducts;


