import React from "react";
import {A} from "../a";
import {createMessageF} from "../../ui/i18n/message";

export const clientHeader = ({client, onChange}, ui) => {
    const m = createMessageF(ui.locale)(require("./locales"));

    if (client == null) {
        return <span>{m("Have an account?")} <A ui={ui.link}>{m("Sign in")}</A></span>;
    }

    return <span>{client.first_name} {client.last_name} <A ui={ui.link} onClick={() => onChange(null)}>{m("Not you?")}</A></span>;
};


