import React from "react";
import cln from "classnames";
import "./details.scss";
import {createMessageF} from "../../../../ui/i18n/message";

export const renderDetails = ({lines, className}) => {
    return (
        <div className={cln("step-common-details", className)}>
            {lines.filter((l) => l).map((line, i) => (
                <div className={cln("line", {"align-label-center": line.component})} key={i}>
                    <div className="label">{line.label}</div>
                    <div className="control">
                        {line.component || line.text}
                    </div>
                </div>
            ))}
        </div>
    );
};


