import cln from "classnames";
import React, {Fragment} from "react";
import {FComponent} from "../../../../../common/f-component";
import {createForm} from "bee-form-react";
import {required, email, equalsPath} from "bee-form-validators";

import "./create-an-account-form.scss";
import {Select} from "../../../../steps/common/select/select";
import {A} from "../../../../../common/a";
import {Button} from "../../../../../common/button";
import {createClient} from "../../../../../api/create-client-api";
import {renderFGInput, renderFGInputPassword, renderFormGroup} from "../../../../steps/common/form/forms";
import {createMessageF} from "../../../../../ui/i18n/message";

const formConfig = {
    "first_name": [required],
    "last_name": [required],
    "email": [required, email],
    "password": [required], // password characters
    "country": [required],
    "confirm_password": [required, equalsPath("password")],
    "zip_code": [required],
};

export class CreateAnAccountForm extends FComponent {

    constructor(props, context) {
        super(props, context);

        this.state = {
            showErrors: false,
            submitting: false,
        };

        this.form = createForm(formConfig);
        this.form.onChange(() => this.forceUpdate());

        if (DEV_QUAN) {
            this.form.setData({
                "first_name": "aaa",
                "last_name": "aaa",
                "email": "aaa@few.com",
                "password": "aaa",
                "country": "US",
                "confirm_password": "aaa",
                "zip_code": "aaa",
            });
        }
    }

    submit(data) {
        const {onCreated} = this.props;

        this.setState({submitting: true});

        createClient(data).then(onCreated);
    }

    render() {
        const {ui} = this.props;
        const {showErrors, submitting} = this.state;

        const fv = this.form.createView();

        const m = createMessageF(ui.locale)(require("./form-locales"));

        return (
            <div className="create-an-account-form">
                <div className="name-line">
                    <div className="col">
                        {fv.withControl("first_name", renderFGInput(m("First Name"), m("First Name"), showErrors))}
                    </div>
                    <div className="col">
                        {fv.withControl("last_name", renderFGInput(m("Last Name"), m("Last Name"), showErrors))}
                    </div>
                </div>

                {fv.withControl("email", renderFGInput(m("Email Address"), m("Email Address"), showErrors))}

                <div className="password-line">
                    <div className="col">
                        {fv.withControl("password", renderFGInputPassword(m("Password"), m("Type in your password"), showErrors))}
                    </div>
                    <div className="col">
                        {fv.withControl("confirm_password", renderFGInputPassword(m("Confirm Password"), m("Type in the same password"), showErrors))}
                    </div>

                    <div className="help">
                        {m("Use a password with mixed-case words and special characters.")}
                    </div>
                </div>


                <div className="country-line">
                    <div className="col">
                        {fv.withControl("country", ({bind, getError, getValue, pushValue}) => renderFormGroup(m("Country"), showErrors && getError(), (
                            <Select
                                value={getValue()}
                                valueToLabel={(v) => ({"US": "United States", "CA": "Canada"}[v])}
                                list={["US", "CA"]}
                                onChange={(value) => pushValue(value)}
                            />
                        )))}
                    </div>
                    <div className="col">
                        {fv.withControl("zip_code", renderFGInput(m("Zip Code"), m("Zip Code"), showErrors))}
                    </div>

                </div>

                <div className="terms">

                    {m(
                        "Clicking $b('Create Account') indicates agreement to our $1 and $2.",
                        <b>{m("Create Account")}</b>,
                        <A ui={ui.link} target="_blank" href="https://www.mytime.com/terms">{m("Terms of Service")}</A>,
                        <A ui={ui.link} target="_blank" href="https://www.mytime.com/terms/privacy">{m("Privacy Policy")}</A>
                    )}
                </div>

                <div className="controls">
                    <Button
                        ui={ui.button}
                        className={cln({disabled: submitting || (fv.hasError() && showErrors)})}
                        onClick={() => fv.hasError() ?
                            !showErrors && this.setState({showErrors: true}) :
                            !submitting && this.submit(fv.getData())
                        }
                    >
                        {m("Create Account")}
                        {submitting && (
                            <Fragment>
                                {" "}
                                <i className="fas fa-circle-notch fa-spin"/>
                            </Fragment>
                        )}
                    </Button>
                </div>
            </div>
        );
    }
}



