import {paddingRight} from "../../../../common/strings";
import React, {Fragment} from "react";
import {css} from "emotion";

export const formatMoneyRange = (range) => {
    if (range.from === range.to) {
        return formatMoney(range.to);
    } else {
        return formatMoney(range.from) + "-" + formatMoney(range.to);
    }
};

export const formatNumber = (num) => {
    let ret = "" + num;
    const dotIndex = ret.indexOf(".");
    if (dotIndex === -1) {
        return ret + ".00";
    } else {
        return ret.substring(0, dotIndex) + "." + paddingRight(ret.substring(dotIndex + 1, dotIndex + 3), 2);
    }
};

export const formatMoney = (num) => {
    return num==null ? "--" : "$" + formatNumber(num);
};


export const formatPrice = (price) => {
    return price == null ? "[error: no price]" :
        price.in_package ? <span className={sPriceInPackage}>In Package</span> :
        price.in_membership ? <span className={sPriceInMembership}>In Membership</span> :
            <Fragment>
                {price.min != null && <Fragment><span className={sPriceMin}>{formatMoney(price.min)}</span> - </Fragment>}

                {formatMoney(price.value)}
            </Fragment>
        ;
};

const sPriceInPackage = css`
  margin-left: 6px;
  background: #3ca13f;

  border-radius: 2px;
  color: #fff;
  padding: 2px 4px;

`;
const sPriceInMembership = sPriceInPackage;

const sPriceMin = css`
  position: relative;
  padding-left: 17px;
  &:before {
    content: "";

    position: absolute;
    left: 0;
    top: 1px;
    width: 15px;
    height: 20px;

    background-image: url("data:image/png;base64,");
    background-position: 0 0;
    background-repeat: no-repeat;
    background-size: 15px;
  }

  color: #df3e1a;
`;


