import cln from "classnames";
import React, {Fragment} from "react";
import {FComponent} from "../../../../common/f-component";

import "../../../layouts/time/time-layout.scss";
import {DatePicker} from "./date-picker/date-picker";
import {TimePicker} from "./time-picker/time-picker";
import {formatPrice} from "../../common/formats";
import {createTimeObject, sameDate} from "../../../../../common/date-object";
import {getOpenTimes} from "../../../../api/get-open-times-api";
import {AsyncAlias} from "../../common/async-alias";
import {Select} from "../../common/select/select";

export class StepTime extends FComponent {

    constructor(props, context) {
        super(props, context);

        this.state = {
            date: createTimeObject(),
            showErrors: false,
        };
    }

    render() {
        const {
            employee, onChangeEmployee,
            employees,

            items,

            onSelect,
            ui,

            render,
        } = this.props;

        const {date, showErrors} = this.state;

        return render({
            services: (
                renderServices({
                    items, employee,
                })
            ),
            showErrors,
            main: (
                <Fragment>
                    <DatePicker
                        date={date}
                        onChange={(date) => this.setState({date})}
                        disabled={() => false}
                        ui={ui}
                    />

                    <AsyncAlias
                        src={date}
                        equals={(d1, d2) => sameDate(d1, d2)}
                        translate={(date) => getOpenTimes(date)}
                        render={(openTimes, loading) => (
                            <TimePicker
                                times={openTimes}
                                loading={loading}
                                onSelect={(time) => {
                                    if (!employee) {
                                        this.setState({showErrors: true});
                                    } else {
                                        onSelect(time);
                                    }
                                }}
                                ui={ui}
                            />
                        )}
                    />
                </Fragment>
            ),
            employeeSelect: (
                <Fragment>
                    <Select
                        value={employee}
                        list={employees.filter((emp) => !items.find((item) => item.getPrice(emp) == null))}
                        onChange={onChangeEmployee}
                        valueToLabel={(emp) => emp.name}
                        placeholder={"Select..."}
                        thin
                    />
                    {showErrors && employee == null && (
                        <div className="error">
                            Employee is required
                        </div>
                    )}
                </Fragment>
            ),
            duration: items.reduce((t, i) => t + i.duration, 0),
        });
    }
}

const renderServices = ({items, employee,}) => (
    <div className="services">
        {items.map((item) => (
            <div className="variation" key={item.id}>
                <div className="name">
                    {item.name}
                </div>

                <div className="price">
                    {formatPrice(item.getPrice(employee))}
                </div>

                <button onClick={item.remove}/>
            </div>
        ))}
    </div>
);


