import React from "react";
import {A} from "../../../../common/a";
import {createMessageF} from "../../../../ui/i18n/message";

export const renderLocationSection = ({location, onChangeLocation, ui}) => {
    const m = createMessageF(ui.locale)(require("./locales"));
    return (
        <section className="location">
            <h4 className="section-title">{m("Your Location")}</h4>

            <div className="content">
                <div className="address-line">
                    {location.street_address}
                </div>
                <div className="address-line">
                    {location.city}{", "}{location.state}
                </div>

                <A ui={ui.link} onClick={onChangeLocation}>{m("Change Location")}</A>
            </div>

        </section>
    );
};




