import React from "react";
import {FComponent} from "./f-component";

export class UiSupport extends FComponent {
    constructor(props, context) {
        super(props, context);

        this.state = {
            hover: false,
        };
    }

    render() {
        const {ui, children, component: Component, ...others} = this.props;
        const {hover} = this.state;

        return (
            <Component
                onMouseOver={() => this.setState({hover: true})}
                onMouseOut={() => this.setState({hover: false})}
                style={ui == null ? null : hover ? ui.hover : ui.normal}
                {...others}
            >
                {children}
            </Component>
        );
    }
}




