import {css} from "emotion";

const defaultButton = css`
  background-color: rgb(75, 116, 144);
  color: rgb(255, 255, 255);
  text-transform: capitalize;
  border: none;
  box-shadow: 0 1px 2px 0 rgba(0,0,0,.3);
  border-radius: 4px;
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  line-height: normal;
  outline: none;

  transition: all .18s ease-in-out;
  font-weight: 700;

  &.disabled, &:disabled {
    opacity: 0.4;
  }
`;

export const button = css`
  ${defaultButton}
  padding: 12px 24px;
`;

export const buttonThin = css`
  ${defaultButton}
  padding: 9px 24px;
`;



