import cln from "classnames";
import React from "react";
import {Select} from "../../../common/select/select";
import {createMessageF} from "../../../../../ui/i18n/message";

export const renderEmpSection = ({employee, onChange, employees, ui}) => {

    const m = createMessageF(ui.locale)(require("./locales"));

    return (
        <section className="employee">
            <h4 className="section-title">{m("You're Booking With")}</h4>

            <div className="content">
                <Select
                    value={employee}
                    valueToLabel={(emp) => emp == null ? m("Anyone") : emp.name}
                    list={[null, ...employees]}
                    onChange={(employee) => onChange(employee)}
                />

                {employee && (
                    <div className={cln("details", {"has-avatar": employee.avatar})}>
                        {employee.avatar && (
                            <div className="avatar" style={{backgroundImage: `url("${employee.avatar}")`}}/>
                        )}

                        {employee.difference_question && (
                            <div className="difference_question">
                                {employee.difference_question}
                            </div>
                        )}
                    </div>
                )}
            </div>
        </section>
    );
};


