import React, {Fragment} from "react";
import {FComponent} from "../../../../common/f-component";

import "./expand.scss";
import {formatPrice} from "../../../steps/common/formats";
import {Button} from "../../../../common/button";
import {A} from "../../../../common/a";
import {createMessageF} from "../../../../ui/i18n/message";

export class HeaderCartExpand extends FComponent {

    render() {
        const {
            purchasing, onChangePurchasing, onSelectTime, getPrice, getName, onClose,
            ui
        } = this.props;

        const m = createMessageF(ui.locale)(require("./locales"));

        return (
            <div className="header-cart-expand">
                <div className="header">
                    {m("Service Selected")}
                </div>

                {(purchasing == null || purchasing.items.length == 0) ? (
                    <div className="empty">
                        {m("Your shopping cart is empty")}
                    </div>
                ) : (
                    <Fragment>
                        <div className="cart-items">
                            {purchasing.items.map((item) => (
                                <div className="item" key={item.id}>
                                    <div className="name">{getName(item)}</div>
                                    <div className="price">{formatPrice(getPrice(item))}</div>
                                    <button
                                        className="btn-remove"
                                        onClick={() => onChangePurchasing(purchasing.items.length === 1 ? null : {
                                            ...purchasing,
                                            items: purchasing.items.filter((pi) => pi !== item)
                                        })}
                                    />
                                </div>
                            ))}
                        </div>

                        <div className="options">
                            <Button ui={ui.button} onClick={() => onSelectTime()}>{m("Select Time")}</Button>
                            <A ui={ui.link} onClick={onClose}>{m("Add another service")}</A>
                        </div>
                    </Fragment>
                )}

            </div>
        );
    }
}



