import cln from "classnames";
import React from "react";
import {FComponent} from "../../../../common/f-component";
import {A} from "../../../../common/a";

import "./payment-info.scss";
import {createMessageF} from "../../../../ui/i18n/message";

export class PaymentInfo extends FComponent {
    constructor(props, context) {
        super(props, context);

        this.state = {
            edit: false,
        };
    }

    render() {
        const {ui, fv} = this.props;
        const {edit} = this.state;

        const m = createMessageF(ui.locale)(require("./payment-info-locales"));

        return (
            <section className="payment-info">
                <h4 className="section-title">{m("Payment Information")}</h4>

                <div className="cancellation-policy">
                    {m("$b('Cancellation Policy'): $2 has a cancellation policy of $money($0) if appointments are cancelled within $1 days of the appointment time.", 5.5, 7, "Petco Grooming")}
                </div>

                <div className="note">
                    {m("Please note: Debit cards holds may cause funds in your bank account to be temporarily unavailable until the hold is released.")}
                </div>

                <div className="learn-more">
                    <A ui={ui.link}>({m("Learn More")})</A>
                </div>
            </section>
        );
    }
}



