const supportedLocales = ["fr", "zh"]; // For validation

const loadLocales = (...localePackages) => {
    let ret = {};
    for (const lp of localePackages) {
        for (const localeName in lp) {
            if (supportedLocales.indexOf(localeName) == -1) {
                throw `Unsupported locale [${localeName}]`;
            }
            ret = {...ret, [localeName]: { ...ret[localeName], ...lp[localeName]}};
        }
    }

    return ret;
};

exports.loadLocales = loadLocales;



