const locationMock = require("./location-mock");
const mock = require("./mock").mock;

const getLocationsApi = (companyId) =>
    mock(locationMock[
        Object.keys(locationMock).find((key) => locationMock[key][0].company_id === companyId)
    ])
;

exports.getLocationsApi = getLocationsApi;



