import React from "react";

import {parseTemplate} from "bee-i18n";

export const formatMsgTemplate = (msgTemplate, resolveVar) => {
    const tokens = parseTemplate(msgTemplate)(resolveVar);

    if (tokens.find((t) => typeof t === "object")) {
        return tokens.map((t, i) => React.createElement(React.Fragment, {key: i}, t))
    }

    return tokens.join("");
};



