import cln from "classnames";
import React from "react";
import {createForm} from "bee-form-react";
import {required, email} from "bee-form-validators";
import {trim, intNumber} from "bee-form-tunnels";
import {FComponent} from "../../../../common/f-component";
import {renderFGInput, renderFormGroup} from "../../common/form/forms";

import "./step-blueprint.scss";
import {Button} from "../../../../common/button";
import {devMode} from "../../../../common/dev-mode";
import {createMessageF} from "../../../../ui/i18n/message";

const formConfig = {
    to: [required],
    from: [required],
    recipient_email: [required, email],
    message: {
        tunnel: [trim],
    },
    amount: {
        validators: [required],
        tunnel: [intNumber],
    },
};

export class StepBlueprint extends FComponent {
    constructor(props, context) {
        super(props, context);

        this.state = {
            showErrors: false,
        };

        this.form = createForm(formConfig, devMode.blueprintData);

        this.form.onChange(() => this.forceUpdate());
    }

    render() {
        const {render, ui, onDone} = this.props;
        const {showErrors} = this.state;

        const fv = this.form.createView();

        const m = createMessageF(ui.locale)(require("./locales"));

        return render({
            content: (
                <div className="step-blueprint-fn34ui9">
                    <div className="title">
                        {m("Buy a Sasson Salon Gift Card")}
                    </div>

                    <div className="form">
                        <div className="col panel-left">
                            <img src="assets/sasson-salon-gift-card.png"/>
                            <div className="note">
                                {m("Valid for any service booked at Sassoon Salon")}
                            </div>
                        </div>
                        <div className="col panel-right">
                            {fv.withControl("to", renderFGInput(m("To:"), "Aunt Mary", showErrors))}
                            {fv.withControl("from", renderFGInput(m("From:"), "Lisa Robinson", showErrors))}
                            {fv.withControl("recipient_email", renderFGInput(m("Recipient Email:"), "mary@email.com", showErrors))}
                            {fv.withControl("message", ({bind, getError}) => renderFormGroup(m("Message to recipient (optional):"), showErrors && getError(), (
                                <textarea
                                    placeholder={m("Happy birthday...")}
                                    {...bind()}
                                />
                            )))}
                            {fv.withControl("amount", renderFGInput(m("Enter amount:"), "$100", showErrors))}

                            <div className="button">
                                <Button
                                    ui={ui.button}
                                    onClick={!fv.isValid() ?
                                        (() => this.setState({showErrors: true})) :
                                        (() => onDone(fv.getData()))
                                    }
                                    disabled={showErrors && !fv.isValid()}
                                >
                                    {m("Continue Checkout")}
                                </Button>
                            </div>
                        </div>
                    </div>
                </div>
            ),
        });
    }
}



